@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

body, input, textarea, button {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
  }

  h1, h2, h3, h4, h5, h6, strong {
    font-weight: 600;
}


@media (max-width: 1080px) {
    *{
        font-size: 93.75%;   
    }

}
  
@media (max-width: 720px) {
    *{
        font-size: 87.5%;
    }
    
}


body{
    background-color: #161C24;
    color: #fff;
}

img{
    width: 380px;
    height: 380px;
    border-radius: 2rem;
    -webkit-box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.22); 
    box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.22);
}

.info{
    margin: 2rem 8rem;
    display: flex;
    gap:3rem;

}

.geral{
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.box{
    background-color: #20262F;
    padding: 2rem;
    border-radius: 2rem;
    -webkit-box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.22); 
    box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.22);

}

p{
    font-weight: 300;
    margin-top: 0.3rem;
}

.baixo{
    margin: 0rem 8rem;
    text-align: center;
}

.texto{
    font-size: 2rem;
}


@media(max-width: 414px){
    h1{
        font-size: 2rem;
    }
    img{
        width: 280px;
        height: 280px;
        border-radius: 2rem;
        -webkit-box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.22); 
        box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.22);
    }
    
    .info{
        margin: 2rem 1rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap:1.7rem;
    
    }
    
    .geral{
        display: flex;
        flex-direction: column;
        text-align: center;
        gap: 1.7rem;
    }
    
    .box{
        background-color: #20262F;
        padding: 2rem;
        border-radius: 2rem;
        -webkit-box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.22); 
        box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.22);
    
    }
    
    p{
        font-size: 1rem;
        font-weight: 300;
        margin-top: 0.3rem;
    }
    
    .baixo{
        margin: 0rem 1rem;
        text-align: center;
    }
    
    .texto{
        font-size: 2rem;
    }
    
    
}